
header[data-v-712d4452]{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 2;
	background: url(../../../static/images/header-bg.png) no-repeat center;
    background-size: cover;
	height: 77px;
    box-shadow: 0 1px 20px 0 rgba(0,0,0,.1);
}
.header-inner[data-v-712d4452]{
	width: 1200px;
	margin: 0 auto;
}
h1[data-v-712d4452]{
	font-size: 30px;
    color: #5d53a7;
	line-height: 77px;
    font-weight: bold;
}
.user-area[data-v-712d4452]{
    display: inline-block;
	/*min-width: 230px;*/
}
.name[data-v-712d4452],.header-img[data-v-712d4452],.out-btn[data-v-712d4452]{
    display: inline-block;
}
.name[data-v-712d4452]{
    color: #5d53a7;
    font-size: 16px;
    line-height: 77px;
    font-weight: 500;
    font-family: '微软雅黑';
}
.header-img[data-v-712d4452],.out-btn[data-v-712d4452]{
    height: 40px;
    width: 40px;
    margin-top: 18.5px;
}
.header-img[data-v-712d4452]{
	border-radius: 50%;
	margin-right: 27px;
	margin-left: 27px;
}
.out-btn[data-v-712d4452]{
    cursor: pointer;
    background: url("../../../static/images/loginOut.png") center no-repeat;
}
.out-btn[data-v-712d4452]:hover{
    background: url("../../../static/images/loginOut2.png") center no-repeat;
}
.login-btn[data-v-712d4452],.register-btn[data-v-712d4452]{
    display: inline-block;
    margin-top: 18.5px;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    overflow: hidden;
    cursor: pointer;
}
.login-btn[data-v-712d4452]{
    margin-right: 28px;
    background: url("../../../static/images/toLogin.png") center no-repeat;
}
.login-btn[data-v-712d4452]:hover{
    background: url("../../../static/images/toLogin2.png") center no-repeat;
}
.register-btn[data-v-712d4452]{
    background: url("../../../static/images/toRegister.png") center no-repeat;
}
.register-btn[data-v-712d4452]:hover{
    background: url("../../../static/images/toRegister2.png") center no-repeat;
}



