}
.details .detailsList .commercial[data-v-b1d61a70]{
    font-size: 20px;
    font-weight: 600;
    line-height: 30px;
    margin-bottom: 90px;
    padding: 0 30px;
}
.details .detailsList .commercialNum[data-v-b1d61a70]{
    /*font-size: 50px;*/
    font-size: 40px;
    font-weight: bold;
    padding: 0 10px;
}
.details .detailsList .starInfo[data-v-b1d61a70]{
    /*width: 670px;*/
    width: 594px;
    height: 100%;
    margin-left: 36px;
    padding-top: 80px;
}
.details .detailsList .starInfo ul[data-v-b1d61a70]{
    width: 100%;
}
.details .detailsList .starInfo li[data-v-b1d61a70]{
    float: left;
