    margin-right: 15px;
    width: 70px;
}
.details .detailsList .starInfo .info[data-v-b1d61a70]{
    width: calc(100% - 100px);
    height: 14px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.detailsList .starInfo .bottom-line .info[data-v-b1d61a70]{
    height: auto;
    white-space: normal;
    max-height: 65px;
}
.details .detailsList .starInfo .awardslist[data-v-b1d61a70]{
    margin-top: -2px;
    display: inline-block;
    width: 100%;
    /*height: 65px;*/
    line-height: 22px;
}
.details .detailsList .starInfo .awardslist li[data-v-b1d61a70]{
    width: 100%;
    margin-bottom: 8px;
    text-align: left;
    color: #fff;
    font-size: 14px;
}
.details .detailsList .imgcon[data-v-b1d61a70]{
    position: relative;
    /*width: 275px;*/
