
.star-details{
    min-height: calc(100vh - 157px);
    background-color: #f2f7ff;
    overflow: hidden;
}
.retrievePasswordWrapper{
    width:1200px;
    margin:0 auto;
}
.retrievePassword-tit{
    padding:30px 0 34px ;
    text-align: left;
    color: #664ab8;
    font-size: 14px;
    cursor: pointer;
}
.retrievePassword-tit .current{
    color:#9c55fd;
    font-size: 16px;
    cursor: default;
}
.retrievePasswordCon{
    min-height:calc(100vh - 283px);
    box-shadow: 0 0 10px 1px #dfeafb;
    border-radius:5px;
    background:#fff;
}
.retrievePasswordConTit{
    padding:30px 0 0 30px;
    line-height:20px;
    text-align:left;
}
.retrievePasswordConTit i{
    width:7px;
    height:16px;
    float:left;
    margin:0 5px 0 0;
    background:url(../../../static/images/titleIco.png) no-repeat;
}
.progress{
    width:880px;
    margin:80px auto 0;
}
.progressBar{
    width:100%;
    height:43px;
    background:url(../../../static/images/retrievePassword1.png) no-repeat;
}
.progressBar2{
    background:url(../../../static/images/retrievePassword2.png) no-repeat;
}
.progressBar3{
    background:url(../../../static/images/retrievePassword3.png) no-repeat;
}
.progressText li{
    float:left;
    width:33.3%;
    padding-top:10px;
    font-size:18px;
    text-align:center;
    color:#999;
}
.progressText li.active{
    color:#7747fd;
}
div.info{
    width:470px;
    margin:0 auto;
    line-height:46px;
    padding-top:100px;
}
div.info li{
    overflow:hidden;
    padding-bottom:30px;
}
div.info li span{
    width:125px;
    float:left;
    font-size:20px;
    color:#666;
    text-align: right;
    margin-right: 5px;
}
.infoInput{
    float:left;
    width:317px;
    height:44px;
    padding:0 10px;
    font-size:18px;
    border:1px solid #d7dbff;
    border-radius:5px;
}
.infoInputYzm{
    width:170px;
}
.infoInput:focus{
    outline: none;
    border-color: #aaa5be;
}
div.info li span.yzm{
    width:135px;
    height:46px;
    float:right;
    font-size:16px;
    cursor:pointer;
    text-align: center;
    background: #9f7cff;
    background: -webkit-linear-gradient(to bottom, #9f7cff, #7848fe);
    background: -moz-linear-gradient(to bottom, #9f7cff, #7848fe);
    background: -ms-linear-gradient(to bottom, #9f7cff, #7848fe);
    background: linear-gradient(to bottom, #9f7cff, #7848fe);
    color: #fff;
    border-radius: 5px;
}
div.info li span.disoprate{
    background: #d7dbff !important;
    color: #fff;
    cursor: default;
}
div.info li span.yzm:hover{
    background: #7848fe;
}
.next{
    text-align:left;
    margin: 20px 0 0 130px;
}
.next a{
    width:120px;
    height:42px;
    line-height: 42px;
    display:inline-block;
    cursor:pointer;
    text-align: center;
    background: #fdcc25;
    background: -webkit-linear-gradient(to bottom, #fdcc25, #fca221);
    background: -moz-linear-gradient(to bottom, #fdcc25, #fca221);
    background: -ms-linear-gradient(to bottom, #fdcc25, #fca221);
    background: linear-gradient(to bottom, #fdcc25, #fca221);
    color: #fff;
    font-size: 18px;
    border-radius: 5px;
}
.next a:hover{
    background: #fca221;
}



